"use client";

import { Bonus } from "@/lib/data";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Star, ArrowRight, Gift, Copy } from "lucide-react";

interface BonusCardProps {
  bonus: Bonus;
}

export function BonusCard({ bonus }: BonusCardProps) {
  return (
    <Card className="group relative overflow-hidden border-0 bg-card/50 shadow-lg transition-all hover:shadow-2xl hover:-translate-y-1 dark:bg-white/5 backdrop-blur-sm ring-1 ring-black/5 dark:ring-white/10">
      {/* Hover Glow Effect */}
      <div className="absolute inset-0 bg-gradient-to-r from-indigo-500/10 via-purple-500/10 to-pink-500/10 opacity-0 transition-opacity duration-500 group-hover:opacity-100" />
      <div className="absolute bottom-0 left-0 h-1 w-full bg-gradient-to-r from-indigo-500 via-purple-500 to-pink-500 transform scale-x-0 transition-transform duration-500 group-hover:scale-x-100" />
      
      <div className="flex flex-col md:flex-row">
        {/* Image Section */}
        <div className="relative h-48 w-full md:h-auto md:w-72 shrink-0 overflow-hidden bg-muted">
             <div className="absolute inset-0 bg-gradient-to-br from-indigo-900/50 to-purple-900/50 flex items-center justify-center group-hover:scale-110 transition-transform duration-700">
                 <Gift className="h-16 w-16 text-white/20" />
            </div>
             
             {bonus.exclusive && (
                <div className="absolute top-4 left-4">
                    <Badge className="bg-yellow-500 hover:bg-yellow-600 text-black font-bold border-none shadow-lg shadow-yellow-500/20">
                        Exclusive
                    </Badge>
                </div>
             )}
        </div>

        {/* Content Section */}
        <div className="relative flex flex-1 flex-col justify-between p-6 sm:p-8">
          <div className="space-y-4">
            <div className="flex items-start justify-between gap-4">
              <div className="space-y-2">
                <h3 className="text-2xl font-bold text-card-foreground group-hover:text-indigo-500 transition-colors">
                  {bonus.title}
                </h3>
                <div className="flex flex-wrap gap-2">
                  {bonus.tags.map((tag) => (
                    <Badge key={tag} variant="outline" className="border-primary/20 bg-primary/5 text-xs font-medium text-muted-foreground">
                      {tag}
                    </Badge>
                  ))}
                </div>
              </div>
              <div className="flex items-center gap-1.5 rounded-lg bg-yellow-500/10 px-3 py-1.5 text-sm font-bold text-yellow-600 dark:text-yellow-400 ring-1 ring-yellow-500/20">
                <span>{bonus.rating}</span>
                <Star className="h-4 w-4 fill-current" />
              </div>
            </div>

            <p className="text-base text-muted-foreground line-clamp-2 leading-relaxed">
              {bonus.description}
            </p>
          </div>

          <div className="mt-8 flex flex-col gap-5 sm:flex-row sm:items-center sm:justify-between border-t border-border/50 pt-5">
             <div className="text-xs font-medium text-muted-foreground/60">
                {bonus.terms}
             </div>
             <div className="flex gap-3">
                 {bonus.code && (
                     <div className="hidden sm:flex items-center gap-2 rounded-lg border border-dashed border-primary/30 bg-primary/5 px-4 py-2 font-mono text-sm font-semibold text-primary transition-colors hover:bg-primary/10 cursor-pointer" title="Click to copy">
                         {bonus.code}
                         <Copy className="h-3 w-3 opacity-50" />
                     </div>
                 )}
                <Button className="w-full sm:w-auto font-bold shadow-lg shadow-primary/20 transition-all hover:scale-105 hover:shadow-primary/40">
                  Get Bonus <ArrowRight className="ml-2 h-4 w-4" />
                </Button>
             </div>
          </div>
        </div>
      </div>
    </Card>
  );
}
