"use client";

import { Button } from "@/components/ui/button";
import { featuredBonus } from "@/lib/data";
import { ArrowRight, Star, ShieldCheck, Zap, Trophy } from "lucide-react";
import { Badge } from "@/components/ui/badge";

export function Hero() {
  return (
    <section className="relative overflow-hidden rounded-3xl bg-black text-white shadow-2xl ring-1 ring-white/10">
      {/* Dynamic Background */}
      <div className="absolute inset-0 bg-[radial-gradient(ellipse_at_top_right,_var(--tw-gradient-stops))] from-purple-900/40 via-background to-background" />
      <div className="absolute -top-[50%] -left-[50%] h-[200%] w-[200%] bg-[radial-gradient(circle_at_center,_var(--tw-gradient-stops))] from-indigo-500/10 via-transparent to-transparent animate-pulse duration-[10000ms]" />
      
      <div className="relative grid gap-12 px-6 py-16 md:grid-cols-2 md:px-12 lg:py-24">
        {/* Left Content */}
        <div className="flex flex-col justify-center space-y-8">
          <div className="flex flex-wrap gap-2">
            <Badge variant="outline" className="border-yellow-500/50 text-yellow-500 bg-yellow-500/10 px-3 py-1 text-sm backdrop-blur-md">
              <Trophy className="mr-1 h-3 w-3" />
              Exclusive Offer
            </Badge>
            {featuredBonus.tags.map((tag) => (
              <Badge key={tag} variant="secondary" className="bg-white/5 text-white/80 hover:bg-white/10 border-white/10 backdrop-blur-sm">
                {tag}
              </Badge>
            ))}
          </div>
          
          <div className="space-y-4">
            <h1 className="text-5xl font-black tracking-tighter sm:text-6xl lg:text-7xl">
              <span className="bg-gradient-to-r from-white via-white to-white/50 bg-clip-text text-transparent">
                {featuredBonus.title}
              </span>
            </h1>
            
            <p className="text-lg text-white/60 sm:text-xl max-w-lg leading-relaxed">
              {featuredBonus.description}
            </p>
          </div>
          
          <div className="flex flex-col gap-4 sm:flex-row">
            <Button size="lg" className="bg-gradient-to-r from-indigo-600 to-purple-600 hover:from-indigo-500 hover:to-purple-500 text-white font-bold text-lg h-14 px-8 shadow-lg shadow-indigo-500/20 transition-all hover:scale-105 hover:shadow-indigo-500/40 border-0">
              Claim Bonus Now
              <ArrowRight className="ml-2 h-5 w-5" />
            </Button>
            {featuredBonus.code && (
              <div className="flex items-center justify-center rounded-md bg-white/5 px-8 py-3 font-mono text-lg font-bold tracking-wider backdrop-blur-sm border border-white/10 text-white/90 select-all">
                {featuredBonus.code}
              </div>
            )}
          </div>
          
          <p className="text-xs text-white/40">
            {featuredBonus.terms}
          </p>
        </div>

        {/* Right Content - 3D Card Effect */}
        <div className="relative hidden md:flex items-center justify-center perspective-1000">
            <div className="relative w-full max-w-sm transform transition-all duration-500 hover:scale-105 hover:rotate-1">
                {/* Glow Effect */}
                <div className="absolute -inset-1 rounded-2xl bg-gradient-to-r from-indigo-500 to-purple-600 opacity-30 blur-2xl" />
                
                <div className="relative rounded-2xl border border-white/10 bg-black/40 p-8 backdrop-blur-xl shadow-2xl">
                    <div className="absolute -top-6 -right-6 flex h-20 w-20 items-center justify-center rounded-full bg-gradient-to-br from-yellow-400 to-orange-500 text-black font-black text-xl shadow-lg shadow-orange-500/20 rotate-12">
                        {featuredBonus.rating}
                    </div>
                    
                    <div className="space-y-8 text-center">
                        <div className="mx-auto flex h-24 w-24 items-center justify-center rounded-full bg-gradient-to-br from-indigo-500/20 to-purple-500/20 ring-1 ring-white/20 shadow-[0_0_40px_-10px_rgba(99,102,241,0.5)]">
                            <Zap className="h-12 w-12 text-indigo-400" />
                        </div>
                        
                        <div className="space-y-2">
                            <h3 className="text-2xl font-bold text-white">Limited Time Offer</h3>
                            <p className="text-white/60">Expires in 24 hours</p>
                        </div>

                         <div className="grid grid-cols-2 gap-4">
                            <div className="flex flex-col items-center rounded-xl bg-white/5 p-4 ring-1 ring-white/10 transition-colors hover:bg-white/10">
                                <ShieldCheck className="mb-2 h-6 w-6 text-emerald-400" />
                                <span className="text-sm font-medium text-white/80">Verified</span>
                            </div>
                             <div className="flex flex-col items-center rounded-xl bg-white/5 p-4 ring-1 ring-white/10 transition-colors hover:bg-white/10">
                                <Zap className="mb-2 h-6 w-6 text-yellow-400" />
                                <span className="text-sm font-medium text-white/80">Instant</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
      </div>
    </section>
  );
}
