"use client";

import { Bonus } from "@/lib/data";
import { BonusCard } from "./bonus-card";
import { Button } from "@/components/ui/button";
import { SlidersHorizontal } from "lucide-react";

interface BonusListProps {
  bonuses: Bonus[];
}

export function BonusList({ bonuses }: BonusListProps) {
  return (
    <section className="space-y-8 py-12">
      <div className="flex flex-col gap-4 md:flex-row md:items-center md:justify-between">
        <div className="space-y-1">
          <h2 className="text-3xl font-bold tracking-tight text-foreground">Top Casino Bonuses</h2>
          <p className="text-muted-foreground">Curated offers just for you</p>
        </div>
        
        <div className="flex items-center gap-2 overflow-x-auto pb-2 md:pb-0 no-scrollbar">
          <Button variant="secondary" size="sm" className="rounded-full bg-primary/10 text-primary hover:bg-primary/20 font-medium">
            All Offers
          </Button>
          <Button variant="ghost" size="sm" className="rounded-full text-muted-foreground hover:text-foreground">
            Exclusive
          </Button>
          <Button variant="ghost" size="sm" className="rounded-full text-muted-foreground hover:text-foreground">
            No Deposit
          </Button>
          <Button variant="ghost" size="sm" className="rounded-full text-muted-foreground hover:text-foreground">
            Free Spins
          </Button>
          <Button variant="outline" size="icon" className="rounded-full ml-2 shrink-0">
            <SlidersHorizontal className="h-4 w-4" />
          </Button>
        </div>
      </div>
      
      <div className="grid gap-6">
        {bonuses.map((bonus) => (
          <BonusCard key={bonus.id} bonus={bonus} />
        ))}
      </div>
    </section>
  );
}
